}

.btn-15:after {
  content: "";
  width: 0;
  height: 100%;
  z-index: -1;
  background-color: rgba(103, 61, 255, .267);
  border-radius: 5px;
  transition: all .4s;
  position: absolute;
  top: 0;
  right: 0;
